import React from 'react'
import { Link } from 'gatsby'
import { ModalRoutingContext } from 'gatsby-plugin-modal-routing'
import "../styles/styles.css"
import image1 from "../../static/DragonRPG/image1.png"

const ModalExamplePage = () => (
  <ModalRoutingContext.Consumer>
    {({ modal, closeTo }) => (
      <div>
        {modal ? (
          <Link to={closeTo} class="close">
            Close
          </Link>
        ) : (
          <header>
            <h1>
              Website Title
            </h1>
          </header>
        )}

        <h1>Dragon RPG</h1>
        
        <img class="rpg" src={image1} alt="image1"/>

        <div>
          <p>
          A unity made RPG/ARPG. This project Is from an online course: RPG Core Combat Creator. The courses aim is to create a basic RPG that could technically be released. The course goes quite thoroughly through the whole game design and creation steps.
          </p> 
        </div>
      </div>
    )}
  </ModalRoutingContext.Consumer>
)

export default ModalExamplePage